  font-weight: 700;
  font-size: 26.5px;
  line-height: 33px;
}

.logo {
  position: relative;
  margin: 0 228px 0 15px;
  font-family: "Orbitron";
  font-weight: 700;
  font-size: 19.9px;
  line-height: 25px;
}
.logo__image {
  position: absolute;
  left: -17px;
