}

.explore-btn {
  font-family: "Orbitron";
  margin-right: 37.6px;
  text-align: center;
  padding: 2px;
  border-radius: 2.2px;
  width: 251px;
  background: linear-gradient(-272.52deg, #FFA6FA 47.49%, #3C79D5 98.81%);
  clip-path: polygon(17% 0, 100% 0, 100% 74%, 86% 100%, 0 100%, 0 31%);
}
.explore-btn__body {
  gap: 10px;
  border-radius: 2.2px;
  width: 100%;
  background: linear-gradient(-92.23deg, #FF56F6 21.43%, #B936EE 50.63%, #3BACE2 100%, #406AFF 117.04%);
  padding: 19px 32px 18px 67px;
  clip-path: polygon(17% 0, 100% 0, 100% 74%, 86% 100%, 0 100%, 0 31%);
}

.place-btn {
  padding: 9px 8px 8px;
  font-weight: 600;
  font-size: 17.7px;
  border: 2px solid transparent;
  border-radius: 4px;
  background: linear-gradient(92.23deg, #FF56F6 21.43%, #B936EE 50.63%, #3BACE2 100%, #406AFF 117.04%) padding-box, linear-gradient(272.52deg, #FFA6FA 47.49%, #3C79D5 98.81%) border-box;
