  color: rgba(255, 255, 255, 0.6);
}

@media (max-width: 991px) {
  .header__row {
    padding: 23px 0 0;
  }
  .header__burger {
    position: fixed;
    top: 27px;
    right: 28px;
    width: 21.7px;
    height: 20px;
    display: flex;
    align-items: center;
    z-index: 10;
  }
  .header__burger span,
.header__burger span::before,
.header__burger span::after {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 2.8px;
    background: #fff;
    border-radius: 1px;
    transition: 0.2s;
  }
  .header__burger span::before {
    content: "";
    top: 8px;
    width: 64%;
    height: 2.8px;
  }
  .header__burger span::after {
    content: "";
    top: 16px;
  }
  .header__burger._active span {
    top: 4px;
    transform: rotate(-45deg);
  }
  .header__burger._active span::before {
    transform: rotate(0);
    top: 0;
  }
  .header__burger._active span::after {
    transform: rotate(90deg);
    top: 0;
  }
  .header__icons {
    margin-left: 0;
    align-self: center;
  }
  .nav-header {
    position: fixed;
    top: 0;
    right: -100%;
    padding: 80px 0 50px;
    min-height: 100vh;
    overflow: auto;
    background: rgb(32, 8, 59);
    flex-direction: column;
    justify-content: space-between;
    gap: 30px;
    visibility: hidden;
    opacity: 0;
    z-index: 9;
  }
  .nav-header__list {
    text-align: center;
    gap: 30px;
    flex-direction: column;
    justify-self: center;
  }
  .nav-header._active {
    visibility: visible;
    opacity: 1;
    right: 0px;
  }
}
@media (max-width: 575px) {
  .header__row {
    font-size: 16.1279px;
    line-height: 20px;
  }
